import React from 'react';
import classes from './PatientPage.css';
import {Link, Route} from 'react-router-dom';
import HeartFrom from '../InputForm/InputForm';
import LungCancerForm from '../InputForm/LungCancerForm';
import CKDForm from '../InputForm/CKDForm';;

class DoctorPage extends React.Component{

    state = {
        loggendIn:true,
        disease : 'heartdisease'
    }

    updateDiseaseForm = (event) => {
        this.setState({
            disease : event.target.value
        })
     }
    render(){

        const FormBlock = () => {
            if(this.state.disease === 'heartdisease'){
                return <HeartFrom target = {false}/>
            }else if(this.state.disease === 'lungcancerdisease'){
                return <LungCancerForm target = {false}/>
            }else if(this.state.disease === 'ckddisease'){
                return <CKDForm target = {false}/>
            }else{
                return null
            }
        }

        return (
            <div className = {classes.container}>
               <div>
               <div className = {classes.dataContainer}>
                        <h2>Check whether you are healthy or not</h2>
                        <div className = {classes.inputContainer}>
                             <label>Select Disease  </label>
                             <select defaultValue = "heartdisease" className = {classes.custom_select} onChange = {this.updateDiseaseForm}>
                                 <option value = "select" className = {classes.option}>select</option>
                                 <option value = "heartdisease">Heart Disease</option>
                                 <option value = "ckddisease">Chronic Kidney Disease</option>
                                 <option value = "lungcancerdisease">Lung Cancer Disease</option>
                             </select>
                        </div>
                        {FormBlock()}
                    </div>
                   
                </div>
            </div>
        )
    }
}


export default DoctorPage;